import React, { useState, useEffect } from "react";
import { Container, Card, Button, Form, InputGroup } from "react-bootstrap";
import RepairShopService from "../service/RepairShopService";
import { Link } from "react-router-dom";

const MobileRepairServices = () => {
  const [repairServices, setRepairServices] = useState([]);
  const [search, setSearch] = useState("");

  const userId = sessionStorage.getItem("userId");

  useEffect(() => {
    RepairShopService.GetAllRepairShop()
      .then((response) => {
        if (response.data) {
          setRepairServices(response.data);
          console.log(response.data);
        }
      })
      .catch((error) => console.error("Error fetching data: ", error));
  }, [search]);

  // Filter repair services based on search query
  const filteredServices = repairServices.filter(
    (service) =>
      service.address.toLowerCase().includes(search.toLowerCase()) ||
      service.specialities.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <Container>
      <InputGroup className="mb-3">
        <Form.Control
          type="text"
          placeholder="Search repair services"
          onChange={(e) => setSearch(e.target.value)}
        />
      </InputGroup>
      {filteredServices.map((service) => (
        <Card key={service.id} className="mb-3">
          <Card.Body>
            <Card.Title>{service.name}</Card.Title>
            <Card.Text>
              <strong>Address:</strong> {service.address}
              <br />
              <strong>Contact:</strong> {service.contactNumber}
              <br />
              <strong>Operating Hours:</strong> {service.operatingHours}
              <br />
              <strong>Specialities:</strong> {service.specialities}
              <br />
              <strong>Email:</strong> {service.email}
              <br />
              <strong>Rating:</strong> {service.rating}
              <br />
              <strong>Review:</strong> {service.review}
            </Card.Text>
            <Link
              variant="success"
              className="mt-2"
              to={`/appointmentform/${userId}/${service.shopId}`}
            >   
             Book Appointment
            </Link>{" "}
          </Card.Body>
        </Card>
      ))}
    </Container>
  );
};

export default MobileRepairServices;
